.t {
  width: 50px;
  height: 25px;
  border-radius: 20px;
  border: 1pz solid rgb(206, 180, 180);
  background-color: rgba(255, 255, 255, 0.74);
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.t-icon {
  width: 15px;
  height: 15px;
}

.t-button {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: rgb(202, 194, 194);
  position: absolute;
  cursor: pointer;
}
